import { HTMLCore } from '../';

export class MainMenu {
    HTMLCore: HTMLCore;
    constructor(HTMLCore: HTMLCore) {
        this.HTMLCore = HTMLCore;
    }
    public InitAppInterfaceMainMenu(color: string) {
        const topDiv = document.createElement('div');
        topDiv.id = 'top';
        // topDiv.style.backgroundColor =  '#'// color; // 'chartreuse';
        topDiv.style.color = '#888';
        this.HTMLCore.host.append(topDiv);

        const h1 = document.createElement('h1');
        h1.textContent = 'NJSLab: Design, Computation, and Data Lab';
        h1.style.paddingLeft = '5px';
        h1.style.marginTop = '0px';
        h1.style.marginBottom = '0px';
        topDiv.append(h1);

        const h2 = document.createElement('h5');
        h2.textContent = 'Sandbox!! Verion 0.1.2';
        h2.style.paddingLeft = '5px';
        h2.style.marginTop = '0px';
        h2.style.marginBottom = '0px';
        topDiv.append(h2);

        const nav = document.createElement('nav');
        nav.style.display = 'flex';
        let menuIndex = document.createElement('ol');
        menuIndex.textContent = 'INDEX';
        menuIndex.style.marginTop = '0px';
        menuIndex.style.paddingLeft = '5px';
        menuIndex.style.paddingRight = '5px';
        menuIndex.onmouseover = () => { menuIndex.style.backgroundColor = 'chartreuse'; };
        menuIndex.onmouseleave = () => { menuIndex.style.backgroundColor = 'white'; };
        menuIndex.onclick = () => {
            const divSubMenu = document.getElementById('demoDiv');
            if (divSubMenu) { this.HTMLCore.host.removeChild(divSubMenu); }
            const divSubMain = document.getElementById('main');
            if (divSubMain) { this.HTMLCore.host.removeChild(divSubMain); }
            this.HTMLCore.InitAppInterfaceMainSuuMenu();
        };
        nav.append(menuIndex);
        this.HTMLCore.host.append(nav);

        let menuNJSTUDIO = document.createElement('ol');
        menuNJSTUDIO.textContent = 'NJSTUDIO';
        menuNJSTUDIO.style.marginTop = '0px';
        menuNJSTUDIO.style.paddingLeft = '5px';
        menuNJSTUDIO.style.paddingRight = '5px';
        // menuNJSTUDIO.setAttribute('href', 'www.njstudio.co.kr');
        menuNJSTUDIO.onmouseover = () => { menuNJSTUDIO.style.backgroundColor = 'chartreuse'; };
        menuNJSTUDIO.onmouseleave = () => { menuNJSTUDIO.style.backgroundColor = 'white'; };
        menuNJSTUDIO.onclick = () => { window.location.href = 'http://www.njstudio.co.kr'; };
        nav.append(menuNJSTUDIO);

        let menuNJSLabEnv = document.createElement('ol');
        menuNJSLabEnv.textContent = 'NJSLAB Env';
        menuNJSLabEnv.style.marginTop = '0px';
        menuNJSLabEnv.style.paddingLeft = '5px';
        menuNJSLabEnv.style.paddingRight = '5px';
        // menuNJSLabEnv.setAttribute('href', 'www.njstudio.co.kr');
        menuNJSLabEnv.onmouseover = () => { menuNJSLabEnv.style.backgroundColor = 'chartreuse'; };
        menuNJSLabEnv.onmouseleave = () => { menuNJSLabEnv.style.backgroundColor = 'white'; };
        menuNJSLabEnv.onclick = () => { window.location.href = 'http://www.njslab.com/NJSLabEnv'; };
        nav.append(menuNJSLabEnv);

        let menuNJSLAB = document.createElement('ol');
        menuNJSLAB.textContent = 'NJSLAB';
        menuNJSLAB.style.marginTop = '0px';
        menuNJSLAB.style.paddingLeft = '5px';
        menuNJSLAB.style.paddingRight = '5px';
        menuNJSLAB.onmouseover = () => { menuNJSLAB.style.backgroundColor = 'chartreuse'; };
        menuNJSLAB.onmouseleave = () => { menuNJSLAB.style.backgroundColor = 'white'; };
        menuNJSLAB.onclick = () => { window.location.href = 'http://www.njslab.com'; };
        nav.append(menuNJSLAB);
        this.HTMLCore.host.append(nav);
    }
}