import { Implementation } from '..';
export class DynamicExternalApps {
    implementation: Implementation;
    constructor(implementation: Implementation) {
        this.implementation = implementation;
    }

    async DemoCodepenCanvas(id: string) {
        const div = document.getElementById(id);
        const iframe = document.createElement('iframe');
        if (iframe && div) {
            iframe.src = 'https://codepen.io/NJStudio/embed/WNQMyjz?height=265&theme-id=dark&default-tab=js,result';
            iframe.style.borderWidth = '0px';
            iframe.style.width = div.style.width; iframe.style.height = div.style.height;
            div.append(iframe);
        }
    }
}