import { SandboxEnv } from '..';
export class ParamApp {
    sandboxEnv: SandboxEnv;
    constructor(sandboxEnv: SandboxEnv) {
        this.sandboxEnv = sandboxEnv;
    }
    public InitParamEnv(param: string) {
        this.sandboxEnv.HTMLCore.RemoveChild(); this.sandboxEnv.HTMLCore.AddMainDiv();
        switch (param) {
            case 'canvas':
                // this.sandboxEnv.njsLabSandBox.implementation.envs.AppEnvCanvas('main'); 
                return true;
            case 'three':
                this.sandboxEnv.njsLabSandBox.implementation.envs.AppEnvThree('main'); return true;
            case 'googlemap':
                this.sandboxEnv.njsLabSandBox.implementation.envs.AppEnvGoogleMap('main'); return true;
            default:
                return false;
        }
    }
    public InitParamApp(param: string) {
        this.sandboxEnv.HTMLCore.RemoveChild(); this.sandboxEnv.HTMLCore.AddMainDiv();
        if (param === 'canvas') {
            // this.sandboxEnv.njsLabSandBox.implementation.envs.AppEnvCanvas('main'); return true;
            //     this.sandboxEnv.njsLabSandBox.implementation.apps.AppCanvasTemplate ('main'); return true;
            // } else if (param === 'three') {
            //     this.sandboxEnv.njsLabSandBox.implementation.apps.AppEnvTHREE('main'); return true;
            // } else if (param === 'svg') {
            //     this.sandboxEnv.njsLabSandBox.implementation.apps.AppEnvSVG('main'); return true;
            // } else if (param === 'ngl3d') {
            //     this.sandboxEnv.njsLabSandBox.implementation.apps.AppEnvNGL3D('main'); return true;
        }
        return false;
    }
    public InitParamDemo(param: string) {
        this.sandboxEnv.HTMLCore.RemoveChild(); this.sandboxEnv.HTMLCore.AddMainDiv();
        console.log('param', param);
        switch (param) {
            case '5': this.sandboxEnv.njsLabSandBox.implementation.demos.Demo5AppThreeGlobeNetwork('main'); return true;
            
            case '20': this.sandboxEnv.njsLabSandBox.implementation.demos.Demo12NGLSL('main'); return true;
            case '21': this.sandboxEnv.njsLabSandBox.implementation.demos.Demo12NWebGL1('main'); return true;
            case '22': this.sandboxEnv.njsLabSandBox.implementation.demos.Demo12NWebGL2('main'); return true;

  
            case '39': this.sandboxEnv.njsLabSandBox.implementation.demos.Demo12NGLSLPlayground('main'); return true;

            case '250': this.sandboxEnv.njsLabSandBox.implementation.demos.Demo250TetrisGame('main'); return true;

            default:
                break;
        }
        return false;
    }
}