import { MainMenu } from './MainMenu';
import { Implementation } from '../../Implementation';
import { SandboxEnv } from '../';
export class HTMLCore {
    sandboxEnv: SandboxEnv;
    color: string;
    imple: Implementation;
    mainMenu: MainMenu;
    allApp: boolean = false;
    host: HTMLDivElement;

    divHostRef: HTMLDivElement;
    constructor(sandboxEnv: SandboxEnv) {
        this.sandboxEnv = sandboxEnv;
        
        
        this.host = document.createElement('div');
        const divHostRef =  document.getElementById(this.sandboxEnv.njsLabSandBox.id);
        divHostRef.appendChild(this.host)
        
        this.mainMenu = new MainMenu(this);


        this.imple = this.sandboxEnv.njsLabSandBox.implementation;
        this.color = 'chartreuse'; // ColorUtility.GetRandomColor();
    }
    public RemoveChild() {
        for (let i = 0; i < this.host.children.length; ++i) {
            this.host.removeChild(this.host.children[i]);
        }
        this.host.style.margin = '0px';
        this.host.style.overflow = 'hidden';
        // debugger;
    }
    public InitAppInterface(allapps: boolean) {
        this.allApp = allapps;
        this.RemoveChild();
        this.mainMenu.InitAppInterfaceMainMenu(this.color);
        this.InitAppInterfaceMainSuuMenu();
    }
    public InitAppInterfaceMainSuuMenu() {
        // this.host.style.overflow = 'auto';
        let indexDiv = document.createElement('div');
        indexDiv.id = 'demoDiv';
        // indexDiv.style.display = 'flex';

        let div: HTMLElement;
        // .................................................................................... Sandbox
        if (this.allApp) {
            let title = document.createElement('h4');
            title.textContent = 'NJSLab Web Sandbox';
            title.style.paddingLeft = '5px';
            title.style.marginBottom = '0px';
            indexDiv.append(title);

            let subMenu = document.createElement('div');
            subMenu.style.display = 'flex';
            subMenu.style.flexWrap = 'wrap'; indexDiv.append(subMenu);


            div = this.GetDivMenu('NThree');
            div.onclick = () => { this.RemoveAddMain(); this.imple.envs.AppEnvThree('main'); };
            subMenu.append(div);

            div = this.GetDivMenu('DrawingBlock');
            div.onclick = () => { this.RemoveAddMain(); this.imple.envs.AppDrawingBlock('main'); };
            subMenu.append(div);

            div = this.GetDivMenu('Algorithm');
            div.onclick = () => { this.RemoveAddMain(); this.imple.envs.AppEnvAlgorithm('main'); };
            subMenu.append(div);


        }
        // .................................................................................... Apps
        let title = document.createElement('h4');
        title.textContent = 'DESIGN SYSTEM - design engineering, agent-base design, parametric system';
        title.style.paddingLeft = '5px';
        title.style.marginBottom = '0px';
        indexDiv.append(title);

        let subMenu = document.createElement('div');
        subMenu.style.display = 'flex';
        subMenu.style.flexWrap = 'wrap'; indexDiv.append(subMenu);


        // .................................................................................... GEOMETRY
        title = document.createElement('h4');
        title.textContent = 'GEOMETRY - analysis, generative design, optimization, simulation';
        title.style.paddingLeft = '5px';
        title.style.marginBottom = '0px';
        indexDiv.append(title);

        subMenu = document.createElement('div');
        subMenu.style.display = 'flex';
        subMenu.style.flexWrap = 'wrap';
        indexDiv.append(subMenu);


        // .................................................................................... VISUALIZATION
        title = document.createElement('h4');
        title.textContent = 'VISUALIZATION';
        title.style.paddingLeft = '5px';
        title.style.marginBottom = '0px';
        indexDiv.append(title);

        subMenu = document.createElement('div');
        subMenu.style.display = 'flex';
        subMenu.style.flexWrap = 'wrap';
        indexDiv.append(subMenu);




        div = this.GetDivMenu('5: Network');
        div.onclick = () => { this.RemoveAddMain(); this.imple.demos.Demo5AppThreeGlobeNetwork('main'); };
        subMenu.append(div);


        // .................................................................................... Machine Learning, Deep Learning
        title = document.createElement('h4');
        title.textContent = 'AI - Machine Learning, Deep Learning ';
        title.style.paddingLeft = '5px';
        title.style.marginBottom = '0px';
        indexDiv.append(title);

        subMenu = document.createElement('div');
        subMenu.style.display = 'flex';
        subMenu.style.flexWrap = 'wrap';
        indexDiv.append(subMenu);


        // .................................................................................... ALGORITHM & DESIGN
        title = document.createElement('h4');
        title.textContent = 'ALGORITHM & DESIGN - spatial data process';
        title.style.paddingLeft = '5px';
        title.style.marginBottom = '0px';
        indexDiv.append(title);

        subMenu = document.createElement('div');
        subMenu.style.display = 'flex';
        subMenu.style.flexWrap = 'wrap';
        indexDiv.append(subMenu);

      
        // .............................................. DESIGN

       

        // .................................................................................... DYNAMICS
        title = document.createElement('h4');
        title.textContent = 'DYNAMICS - Particle, Simulation';
        title.style.paddingLeft = '5px';
        title.style.marginBottom = '0px';
        indexDiv.append(title);

        subMenu = document.createElement('div');
        subMenu.style.display = 'flex';
        subMenu.style.flexWrap = 'wrap';
        indexDiv.append(subMenu);



        div = this.GetDivMenu('250: Tetris Game');
        div.onclick = () => { this.RemoveAddMain(); this.imple.demos.Demo250TetrisGame('main'); };
        subMenu.append(div);



        // .................................................................................... STARTER
        title = document.createElement('h4');
        title.textContent = 'STARTER';
        title.style.paddingLeft = '5px';
        title.style.marginBottom = '0px';
        indexDiv.append(title);

        subMenu = document.createElement('div');
        subMenu.style.display = 'flex';
        subMenu.style.flexWrap = 'wrap';
        indexDiv.append(subMenu);

        div = this.GetDivMenu('14: Codepen Canvas');
        div.onclick = () => {
            this.RemoveAddMain(); this.imple.externalApps.DemoCodepenCanvas('main');
        };
        subMenu.append(div);


        div = this.GetDivMenu('20: GLSL Playground');
        div.onclick = () => { this.RemoveAddMain(); this.imple.demos.Demo12NGLSL('main'); };
        subMenu.append(div);

        div = this.GetDivMenu('5: Network');
        div.onclick = () => { this.RemoveAddMain(); this.imple.demos.Demo12NGLSLPlayground('main'); };
        subMenu.append(div);

        div = this.GetDivMenu('WebGPU');
        div.onclick = () => { this.RemoveAddMain(); this.imple.demos.Demo13NWebGPU2('main'); };
        subMenu.append(div);

        div = this.GetDivMenu('21:WebGL1');
        div.onclick = () => { this.RemoveAddMain(); this.imple.demos.Demo12NWebGL1('main'); };
        subMenu.append(div);

        div = this.GetDivMenu('22:WebGL2');
        div.onclick = () => { this.RemoveAddMain(); this.imple.demos.Demo12NWebGL2('main'); };
        subMenu.append(div);




        this.host.append(indexDiv);
    }
    public GetDivMenu(title: string, hoverEffect: boolean = true) {
        let menu = document.createElement('ol');
        menu.textContent = title;
        menu.style.paddingLeft = '5px';
        menu.style.paddingRight = '5px';
        if (hoverEffect) {
            menu.onmouseover = () => { menu.style.backgroundColor = this.color; }; // 'chartreuse'; };
        }
        menu.onmouseleave = () => { menu.style.backgroundColor = 'white'; };
        return menu;
    }
    public RemoveAddMain() {
        const div = document.getElementById('demoDiv');
        if (div) { this.host.removeChild(div); }
        const divMain = document.createElement('div');
        divMain.id = 'main';
        divMain.style.height = '100%';
        divMain.style.width = '100%';
        this.host.style.overflow = 'hidden';
        window.scrollTo({ top: 0, behavior: 'smooth' });
        this.host.append(divMain);
    }
    public AddMainDiv() {
        const div = document.createElement('div');
        if (div) {
            div.id = 'main';
            div.style.width = '100%';
            div.style.height = '100%';
            this.host.append(div);
            // console.log(div);
        }
    }
}