import { DynamicImportApps } from './Apps';
import { DynamicImportDemo } from './Demos';
import { DynamicImportEnvs } from './Envs';
import { DynamicExternalApps } from './ExternalApps';
import { NJSLabSandboxCore } from '../';
export class Implementation {
    njsSandCore: NJSLabSandboxCore;
    apps: DynamicImportApps;
    demos: DynamicImportDemo;
    envs: DynamicImportEnvs;
    externalApps: DynamicExternalApps;
    constructor(njsSandCore: NJSLabSandboxCore) {
        this.njsSandCore = njsSandCore;
        this.apps = new DynamicImportApps(this);
        this.demos = new DynamicImportDemo(this);
        this.envs = new DynamicImportEnvs(this);
        this.externalApps = new DynamicExternalApps(this);
    }
}

