import { ParamApp } from './ParamApp';
import { HTMLCore } from './HTMLCore';
import { NJSLabSandboxCore } from '..';
export class SandboxEnv {
    njsLabSandBox: NJSLabSandboxCore;
    paramApp: ParamApp;
    HTMLCore: HTMLCore;
    allapps: boolean = false;
    constructor(njsLabSandBox: NJSLabSandboxCore) {
        this.njsLabSandBox = njsLabSandBox;
        this.HTMLCore = new HTMLCore(this);
        this.paramApp = new ParamApp(this);
    }
    public InitParam(param: any) {

        const obj = param as object;
        if (obj[`env`]) {
            if (this.paramApp.InitParamEnv(param['env'])) {
                return;
            }
        } else if (obj[`app`]) {
            if (this.paramApp.InitParamApp(param['app'])) {
                return;
            }
        } else if (obj[`demo`]) {
            if (this.paramApp.InitParamDemo(param['demo'])) {
                return;
            }
        } else if (obj[`all`]) {
            this.allapps = true;
        }
        this.HTMLCore.InitAppInterface(this.allapps);
    }
}