import { Implementation } from './Implementation';
import { BrowserUtility } from './NJSCore';
import { SandboxEnv } from './SandboxEnv';

// import { Color, version } from 'njscore';
import { Color, version } from '../../lib';

export class NJSLabSandboxCore {
    public static appCore: NJSLabSandboxCore;
    public static init(id: string) {
        this.appCore = new NJSLabSandboxCore(id);
    }
    public implementation: Implementation;
    public sandboxEnv: SandboxEnv;
    public id: string;
    constructor(id: string) {
        this.id = id;
        this.implementation = new Implementation(this);
        this.sandboxEnv = new SandboxEnv(this);
        this.InitParam();

        console.log('njscore',version);
        // console.log(Color)

        // console.log(new Color(255, 255, 0))
        // console.log(JSON.stringify(this));
    }
    public InitParam() {
        let param = BrowserUtility.getAllUrlParams();
        param = {
            all: true
            // demo: '19'
        }
        // https://namjulee.github.io/njs-lab-public/njslab-legacy/?demo=19
        // console.log(param);
        this.sandboxEnv.InitParam(param);
    }
}


